
import Picker from 'vux/src/components/picker/index.vue'

export default {
components: {
Picker
},
data(){
return {
account: '',
pass: '12345678',
pickerList:[{
name: '党支部书记',
value: "3"
}, {
name: '片区负责人',
value: "2"
}],
pickerValue: "3",
roleName : "党支部书记",
PickerVisible:false
}
},
methods:{
login() {






if (!this.account)
return this.$vux.toast.show({
text: '请输入帐号',
type: 'text',
position: 'top'
});
if (!this.pass)
return this.$vux.toast.show({
text: '请输入密码',
type: 'text',
position: 'top'
});
this.$store.dispatch('login', {
name: this.account,
password: this.pass,
openId: this.$route.query.openId || undefined
})
.then(
result =>{
if(result.status){
var roleId = result.entry.roleId;
this.$router.push({
path: '/main' + roleId
})
}else{
this.$vux.toast.show({
text: result.message,
width: '18em',
type: 'text',
position: 'top'
})
}
}
)
},
keyUpReturn()
{
this.login();
},
roleChange(value){
this.pickerValue = value;
if(value == "3")
{
this.roleName = this.pickerList[0].name;
}
else if(value == "2")
{
this.roleName = this.pickerList[1].name;
}
}

},
mounted(){

}
}



